
.lists, .add {
  box-sizing: border-box;
  padding: 10px;
  transition: width 1s;
  cursor: pointer;
  width: 100%;
}
@media (min-width: 768px) {
.lists, .add {
      width: 50%;
}
}
@media (min-width: 992px) {
.lists, .add {
      width: 33.3333%;
}
}
@media (min-width: 1200px) {
.lists, .add {
      width: 25%;
}
}
.lists .list, .add .list {
    background: #1e2b3a;
    height: 87px;
    padding: 10px;
    border-radius: 6px;
    transition: all 0.3s ease;
}
.lists .list .title, .add .list .title {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
          align-items: center;
      padding-bottom: 10px;
}
.lists .list .title h3, .add .list .title h3 {
        font-size: 16px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding-left: 10px;
        text-transform: capitalize;
}
.lists .list h4, .add .list h4 {
      font-size: 13px;
      color: #a2a7ad;
      display: -webkit-box !important;
      overflow: hidden;
      text-overflow: ellipsis;
      word-break: break-all;
      -webkit-line-clamp: 2;
}
.lists .list .noTitle .paper, .add .list .noTitle .paper {
      width: 80px;
      height: 80px;
}
.lists .list .noTitle .paper i, .add .list .noTitle .paper i {
        font-size: 32px;
        text-transform: capitalize;
}
.lists .list .paper, .add .list .paper {
      width: 40px;
      height: 40px;
      background-color: #607d8b;
      background-size: cover;
      overflow: hidden;
      border-radius: 50%;
      -ms-flex-negative: 0;
          flex-shrink: 0;
      box-shadow: 0.08rem 0.16rem 0.26667rem rgba(59, 59, 60, 0.7);
      position: relative;
}
.lists .list .paper i, .add .list .paper i {
        position: absolute;
        font-style: normal;
        width: 40px;
        line-height: 40px;
        text-align: center;
        margin-left: -20px;
        margin-top: -20px;
        left: 50%;
        top: 50%;
        color: #FFF;
        font-size: 18px;
}
.lists .list:hover, .add .list:hover {
      transform: translateY(-3px);
      box-shadow: 0 26px 40px -24px rgba(0, 0, 0, 0.67);
      transition: all 0.3s ease;
}



