
a {
  color: #d1d4d7;
}
.right {
  transition: margin 1s;
}
.left {
  position: fixed;
  top: 0;
  left: -50%;
  height: 100%;
  overflow: auto;
  background: #121c29;
  z-index: 999;
  transition: left 2s;
}
@media (max-width: 768px) {
.left {
      display: none;
}
}
.left .nav-element {
    cursor: pointer;
    padding-bottom: 2px;
    color: #7e848b;
    transition: color 0.25s;
}
.left .nav-element svg {
      margin-right: 6px;
}
.left .navbar-active {
    color: #28e4d8;
}
.left .nav-label {
    font-size: 12px;
    padding-left: 10px;
}
.left .nav-label svg {
      width: 16px;
      height: 16px;
}
.left li {
    width: 100%;
}
.left li:hover {
    opacity: 1;
    background-color: #1e2b3a;
}
.modal-active .blur {
  filter: blur(0.08rem);
}
#loading {
  display: none;
}
.container, .router-view {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.icon {
  width: 20px;
  height: 20px;
  vertical-align: text-bottom;
  fill: currentColor;
  overflow: hidden;
}
.vux-pop-out-enter-active,
.vux-pop-out-leave-active,
.vux-pop-in-enter-active,
.vux-pop-in-leave-active {
  will-change: transform;
  height: 100%;
  position: absolute;
  left: 0;
}
.vux-pop-out-enter-active {
  animation-name: popInLeft;
}
.vux-pop-out-leave-active {
  animation-name: popOutRight;
}
.vux-pop-in-enter-active {
  perspective: 1000;
  animation-name: popInRight;
}
.vux-pop-in-leave-active {
  animation-name: popOutLeft;
}
@keyframes popInLeft {
from {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
}
to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
}
}
@keyframes popOutLeft {
from {
    opacity: 1;
}
to {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
}
}
@keyframes popInRight {
from {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
}
to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
}
}
@keyframes popOutRight {
from {
    opacity: 1;
}
to {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
}
}



